.block_cover_item {
  margin: 0 auto;
  width: 960px;
}


.block_item {
  display: flex;
  justify-content: space-between;
  padding: 15px 0;
}

.block_image {
  width: 40%;
}

.block_text {
  width: 60%;
  height: auto;
  padding: 0 20px 0 20px;
}

.titleselect {
  text-shadow: 0 0 3px rgb(94, 79, 84), 0 0 7px rgb(79, 91, 112);
  color: #535258;
  font-size: 30px;
  font-weight: 400;
  margin: 0;
  text-transform: uppercase;
  width: 100%;
  text-align: center;
  padding: 30px;
  margin-bottom: 40px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px,
    rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

.titleselect:hover {
  color: #3638ac;
}

.block_cover_item h1 {
  padding: 0;
  margin: 0;
  display: block;
  position: relative;
  color: #3a85ae;
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 20px;
}

p {
  font-family: "Raleway-Regular", Arial, Helvetica, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.3;

  color: rgb(124, 123, 123);
}



@media (max-width: 960px) {
 
.block_item {
flex-direction: row;

}
.block_cover_item {

  width:auto;
}
.titleselect {
 
  font-size: 10px;
  font-weight: 300;
  margin: 0;
 }
 .block_text p {
   font-size: 10px;
   padding: 0;
 }
        }

        @media (max-width: 480px) {
 
        
           .block_text p {
             font-size: 6px;
             padding: 0;
           }
                  }
